* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
li{
    list-style: none;
}
a{
    text-decoration: none;
    color: inherit;
}
body{
    color: #333;
}

.check{
    max-width: 640px;
    width: calc(100% - 30px);
    margin: 20px auto;
}
.check form input[type="text"]{
    width: 100%;
    height: 50px;
    border: 1px solid #548805;
}

.check form img {
    width: 24px;
    height: 24px;
    object-fit: cover;
}
.check .check_list{
    width: 100%;
    margin: 0 auto;
    height: 100%;
    text-align: left;
}
.check .check_list ul{
    text-align: start;
    width: 100%;
    height: 20px;
}

.check .check_list ul li{
    margin-top: 40px;
    width: 100%;
    height: 40px;
    line-height: 40px;
    background-color: #548805;
    color: #f5f5f5; 
}
.check .check_list ul strong{
    font-size: 24px;
    padding: 0 20px;
}
.check .check_list ul img{
    width: 24px;
    height: 24px;
}